import { toast } from 'react-toastify';

const toastStyle = {
  success: {
    background: '#e9f7f0',
    color: 'rgb(82, 75, 75)',
    borderRadius: '50px',
    fontFamily: 'Manrope',
    fontSize: '16px',
  },
  error: {
    background: '#f97e93e9',
    color: '#ffffff',
    borderRadius: '50px',
    fontFamily: 'Manrope',
    fontSize: '16px',
  },
  info: {
    background: '#aed2f4',
    color: '#ffffff',
    borderRadius: '50px',
    fontFamily: 'Manrope',
    fontSize: '16px',
  },
};

export const showToast = (type, message) => {
  toast(message, {
    style: toastStyle[type],
  });
};
